export function window2Canvas (canvas, clientX, clientY) {
  // è·åcanvasåç´ çè¾¹çæ¡ï¼è¯¥è¾¹çæ¡çåæ æ¯ç¸å¯¹äºæ´ä¸ªçªå£ç
  const box = canvas.getBoundingClientRect()
  const w_scale = canvas.width / box.width
  const h_scale = canvas.height / box.height

  return {
    x: (clientX - box.left) * w_scale,
    y: (clientY - box.top) * h_scale
  }
}

export function getPixelRatio (context) {
  // æµè§å¨å¨æ¸²æcanvasä¹åå­å¨ç»å¸ä¿¡æ¯çåç´ æ¯
  const devicePixelRatio = window.devicePixelRatio || 1;
  const backingStoreRatio = context.backingStorePixelRatio ||
      context.webkitBackingStorePixelRatio ||
      context.mozBackingStorePixelRatio ||
      context.msBackingStorePixelRatio ||
      context.oBackingStorePixelRatio ||
      context.backingStorePixelRatio || 1;
  return devicePixelRatio / backingStoreRatio;
}

/**
 * ç»å¶åè§ç©å½¢
 *
 * @export
 * @param {Number} left ç©å½¢å·¦ä¸è§æ¨ªåæ 
 * @param {Number} top ç©å½¢å·¦ä¸è§çºµåæ 
 * @param {Number} width ç©å½¢å®½åº¦
 * @param {Number} height ç©å½¢é«åº¦
 * @param {Number} r ç©å½¢åè§åå¾
 * @param {Object} ctx ç»å¸å¯¹è±¡
 */
export function drawRadiusRect (left, top, width, height, r, ctx) {
  const pi = Math.PI
  ctx.beginPath()
  ctx.arc(left + r, top + r, r, -pi, -pi / 2)
  ctx.arc(left + width - r, top + r, r, -pi / 2, 0)
  ctx.arc(left + width - r, top + height - r, r, 0, pi / 2)
  ctx.arc(left + r, top + height - r, r, pi / 2, pi)
  ctx.closePath()
}

export function drawCircle (x, y, r, ctx) {
  // var offset = 1;
  const pi = Math.PI
  ctx.beginPath()
  ctx.arc(x, y, r, 0, pi * 2, true)
  ctx.closePath()
}

export function drawCross (x, y, size, width, color, cap, ctx) {
  ctx.lineWidth = width
  ctx.strokeStyle = color
  ctx.beginPath()
  ctx.moveTo(x - size / 2, y - size / 2)
  ctx.lineTo(x + size / 2, y + size / 2)
  ctx.lineCap = cap
  ctx.stroke()
  ctx.closePath()

  ctx.beginPath()
  ctx.moveTo(x + size / 2, y - size / 2)
  ctx.lineTo(x - size / 2, y + size / 2)
  ctx.lineCap = cap
  ctx.stroke()
  ctx.closePath()
}
